import {Link, withRouter} from "react-router-dom";
import {useActiveWeb3React} from "../../hooks";
import Img from "../UI/Img";
import {rates} from "../../constants";
import {useDispatch, useSelector} from "react-redux";
import * as actions from '../../state/currency/actions';

const Wrapper = styled.div`
  position: relative;
  padding: 14px 0;
  
  &:hover .header-dropdown {
    opacity: 1;
    visibility: visible;
    transform: rotateX(0deg) scale(1);
  }
`

const Item = styled.div`
  display: flex;
  align-items: center;
`

const Button = styled.button`
  margin-right: .75rem;
`

const DropDown = styled.div`
  position: absolute;
  top: 100%;
  right: 0;
  background-color: ${({ theme }) => theme.modalBG};
  border-radius: .75rem;
  box-shadow: -1px 11px 43px rgba(0, 0, 0, 0.12);
  padding: 20px;
  
  opacity: 0;
  visibility: hidden;
  transform-style: preserve-3d;
  transform: rotateX(-40deg) scale(0.8);
  transform-origin: top center;
  transition: 0.4s ease all;
  z-index: 99999;
  
`

const DropDownItem = styled(Link)`
  display: flex;
  align-items: center;
  position: relative;
  outline: none;
  text-decoration: none;
  white-space: nowrap;
  color: ${({ theme }) => theme.text1};
  
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  
  &:hover,
  &:focus,
  &:active {
    color: ${({ theme }) => theme.text2};
    box-shadow: none;
    outline: none;
    text-decoration: none;
  }
`

const CurrencyLogo = styled(Img)`
  width: 24px;
  height: 24px;
  margin-right: 20px;
  border-radius: 24px;
  border: 1px solid #7985F6;
  min-width: 24px;
`


const FlagButton = styled(BootstrapButton)`
  margin-right: 12px;
`

const Title = styled.span`
  font-weight: 500;
  font-size: 1rem;
  color: #D3D6E8;
`


const HeaderDropdown = ({items, title, ...props}) => {
    const selectedCurrency = useSelector(state => state.currency.selected)
    const dispatch = useDispatch();


    const selectDestinationCurrency = (id) => {
        dispatch(actions.fetchCurrencies(id));
    }

    useEffect(() => {
        dispatch(actions.fetchCurrencies(selectedCurrency));
    }, [selectedCurrency, dispatch]);

    return (
        <Wrapper>
            <Item>
                <FlagButton variant={'link'} className={'py-0 px-0'}>
                    <Img src={require('../../assets/images/header/flag.svg').default} alt={''}/>
                </FlagButton>
            </Item>
            <DropDown className={'header-dropdown'}>
                {Object.keys(rates).map((currency) => (
                    <DropDownItem
                        onClick={selectDestinationCurrency.bind(this, currency)}
                        key={`currency-${currency}`}
                    >
                        <CurrencyLogo src={rates[currency].image} alt={currency}/>
                        <Title>
                            {currency}
                        </Title>
                    </DropDownItem>
                        // <a
                        //     onClick={selectDestinationCurrency.bind(this, currency)}
                        //     className="navi-link"
                        // >
                        //         <span className="symbol symbol-20 mr-3">
                        //             <img src={rates[currency].image} alt={currency} />
                        //         </span>
                        //     <span className={`navi-text ${darkMode ? 'text-white' : 'text-dark-75'}`}>{currency}</span>
                        // </a>
                ))}
                {/*{Object.values(items).map((item, index) => {*/}
                {/*    return (*/}
                {/*        <DropDownItem to={item.path} key={`${title}-${index}`}>{item.title}</DropDownItem>*/}
                {/*    )*/}
                {/*})}*/}
            </DropDown>
        </Wrapper>
    )
}

export default withRouter(HeaderDropdown);
