import { Row, Col } from 'react-bootstrap';
import {useEffect, useCallback, useState, useMemo} from 'react';
import {Network, OpenSeaPort} from "opensea-js";
import styled from 'styled-components';
import Skeleton from "react-loading-skeleton";
import SVG from 'react-inlinesvg';

import Page from "../../components/Page";
import {useActiveWeb3React} from "../../hooks";
import StyledCard, { ResponsiveCard } from "../../components/Card";
import CollapseCard from "../../components/CollapseCard";
import Web3 from "web3";
import Img from "../../components/UI/Img";
import {shorten} from "../../state/governance/hooks";
import {getEtherscanLink} from "../../utils";
import OpenSeaApi from "../../http/opensea";

const web3 = new Web3(process.env.REACT_APP_WSS_URL);
const seaport = new OpenSeaPort(web3.givenProvider || window.ethereum, {
    networkName: Network.Main
})
const api = new OpenSeaApi();

const Container = styled(StyledCard)`
  & .card-body {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
`

const Image = styled(Img)`
  width: 100%;
  display: block;
  height: 100%;
  min-height: 390px;
  object-fit: contain;
  border-radius: 18px;
  background-color: rgba(255, 255, 255, 1);
`

const Description = styled.span`
  font-weight: 400;
  font-size: 1rem;
  line-height: 19px;
  color: ${({ theme }) => theme.text1};
`

const OwnerImage = styled(Img)`
  width: 48px;
  height: 48px;
  border-radius: 100px;
  background-color: rgba(71, 183, 254, 0.15);
  
`

const OwnerAddress = styled.span`
  font-size: 1rem;
  font-weight: 500;
  color: ${({ theme }) => theme.primary};
  line-height: 1;
  margin-left: 10px;
`



const CollectionLogoContainer = styled.div`
  width: 32px;
  height: 32px;
  min-width: 32px;
  border-radius: 32px;
  margin-left: 12px;
`

const CollectionLogo = styled(Img)`
    width: 100%;
    height: 100%;
    border: 1px solid white;
    border-radius: 320px;
    
`

const ChainRow = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  &:not(:last-child) {
    margin-bottom: 20px;
  }
`

const ChainLabel = styled.span`
  color: ${({ theme }) => theme.text1};
  font-size: .875rem;
  font-weight: 400;
`

const ChainValue = styled.span`
  color: ${({ theme }) => theme.text1};
  font-size: 1rem;
  font-weight: 400;
`

const ChainLink = styled.a`
  color: ${({ theme }) => theme.primary1};
  font-size: 1rem;
  font-weight: 400;
  
  &:active,
  &:focus {
    outline: none;
  }
`
const PropertyGrid = styled.div`
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
`
const PropertyLabel = styled.div`
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  background-color: rgba(71, 183, 254, 0.15);
  margin: 0 10px 10px 0;
  
  
`

const PropertyText = styled.span`
  font-size: .75rem;
  font-weight: 400;
  color: ${({ theme }) => theme.primary};
  line-height: 1;
  display: block;
  margin-bottom: 6px;
`
const PropertyValue = styled.span`
  font-size: .875rem;
  font-weight: 500;
  color: ${({ theme }) => theme.primary};
  line-height: 1;
`

const CollectionName = styled.span`
  text-decoration: underline;
  color: ${({ theme }) => theme.text1};
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  margin-bottom: 1rem;
`

const Name = styled.h2`
  margin-top: 0;
  margin-bottom: 1.5rem;  
  font-size: 1.875rem;
  font-weight: 700;
  color: ${({ theme }) => theme.text1};
`

const Socials = styled.div`
  display: flex;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 4px;
  
`

const SocialLink = styled.a`
  text-decoration: none;
  display: block;
  
  &:not(:last-child) {
    margin-right: 24px;
  }
  
  &:hover,
  &:active,
  &:focus {
    outline: none;
    text-decoration: none;
  }
`

const Details = styled.div`
  display: flex;
  align-items: center
`

const NFTAsset = props => {
    const { account } = useActiveWeb3React();
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [asset, setAsset] = useState(null)
    const { asset_contract, token_id } = props.match.params;

    const fetchSeaAsset = useCallback(async (contract, id) => {
        try {
            setLoading(true)
            setError(false);
            const res = await api.get('singleAsset', {
                tokenAddress: contract,
                tokenId: id,
            })
            setAsset(res?.data)
        } catch(e) {
            setError(true);
        } finally {
            setLoading(false);
        }
    }, [asset_contract, token_id])

    const traits = useMemo(() => {
        return asset ? asset?.traits.filter(t => !!t.value && typeof t.value === 'string') : [];
    }, [asset])

    useEffect(() => {
        if(error) {
            props.history.push('/invest/nft');
        }
    }, [error])

    useEffect(() => {
        if(!asset_contract || !token_id) {
            props.history.push('/invest/nft');
        } else {
            fetchSeaAsset(asset_contract, token_id);
        }
    }, [asset_contract, token_id])

    return (
        <Page title={'NFT'} morePadding>
            <Row className={'d-flex flex-row-reverse custom-row'} style={{ marginTop: -30 }}>
                <Col xs={12} md={8}>
                    <Container>
                        <div className="d-flex align-items-start justify-content-between">
                            <div className="d-flex align-items-start flex-column">
                                <CollectionName>
                                    {loading ? (
                                        <Skeleton height={18} width={100}/>
                                    ) : asset?.collection?.name}
                                </CollectionName>
                                <Name>
                                    {loading ? (
                                        <>
                                            <Skeleton height={32} width={140} className={'mr-3'}/>
                                            <Skeleton height={32} width={60}/>
                                        </>
                                    ) : asset?.name}
                                </Name>
                                {!loading && (
                                    <Details>
                                        {asset?.top_ownerships?.length > 1 ? (
                                            <div className="d-flex align-items-center">
                                                <SVG src={require('../../assets/images/nft/owners.svg').default}/>
                                                <span className="pl-2">{asset?.top_ownerships?.length} Owners</span>
                                            </div>
                                        ) : (

                                            <div className="d-flex align-items-center">
                                                <SVG src={require('../../assets/images/nft/owner.svg').default}/>
                                                <span className="pl-2">Owned by {shorten(asset?.owner?.address, 'choice')}</span>
                                            </div>
                                        )}
                                    </Details>
                                )}
                            </div>
                        </div>
                    </Container>
                    <CollapseCard title={'Properties'}>
                        <PropertyGrid>
                            {traits.length > 0 ? traits.map(t => {
                                return (
                                    <PropertyLabel>
                                        <PropertyText>{t.trait_type}</PropertyText>
                                        <PropertyValue>{t.value.split('_').join(" ")}</PropertyValue>
                                    </PropertyLabel>
                                )
                            }) : (
                                <div className="d-flex align-items-center jusify-content-center flex-column w-100 py-5">
                                    <span className="font-size-sm font-weight-normal text-center">There is nothing to show</span>
                                </div>
                            )}
                        </PropertyGrid>
                    </CollapseCard>
                </Col>
                <Col xs={12} md={4}>
                    <Container>
                        {loading ? (
                            <Skeleton width={'100%'} height={'430px'} style={{
                                borderRadius: 18
                            }}/>
                        ) : (
                            <Image src={asset?.image_url} alt={asset?.name}/>
                        )}
                    </Container>
                    <CollapseCard title={'Details'}>
                        <div className="d-flex flex-column">
                            <div className="d-flex align-items-center mb-4">
                                {loading ? (
                                    <Skeleton circle width={48} height={48}/>
                                ) : (
                                    <OwnerImage src={asset?.creator?.profile_img_url} alt={asset?.creator?.profile_img_url}/>
                                )}
                                <OwnerAddress>{loading ? (
                                    <Skeleton width={100} height={24}/>
                                ) : shorten(asset?.creator?.address, 'choice')}</OwnerAddress>
                            </div>
                            {!loading ? (
                                <Description>
                                    {asset?.description}
                                </Description>
                            ) : (<Skeleton count={4}/>)}
                        </div>
                    </CollapseCard>
                    <CollapseCard title={(
                        <div className={'d-flex align-items-center'}>
                            {loading ? (<Skeleton width={60} height={24}/>) : asset?.collection?.name}
                            <CollectionLogoContainer>
                                {loading ? (
                                    <Skeleton circle width={32} height={32}/>
                                ) : (
                                    <CollectionLogo src={asset?.collection?.image_url} alt={asset?.collection?.name}/>
                                )}
                            </CollectionLogoContainer>
                        </div>
                    )}>
                        {!loading ? (
                            <Description>
                                {asset?.collection?.description}
                            </Description>
                        ) : (<Skeleton count={4}/>)}
                        <Socials>
                            <SocialLink target={'_blank'} rel={'noopener noreferrer'} href={asset?.collection?.external_url}>
                                <SVG src={require("../../assets/images/nft/website.svg").default} />
                            </SocialLink>
                            <SocialLink target={'_blank'} rel={'noopener noreferrer'} href={asset?.collection?.telegram_url}>
                                <SVG src={require("../../assets/images/nft/telegram.svg").default} />
                            </SocialLink>
                            <SocialLink target={'_blank'} rel={'noopener noreferrer'} href={asset?.collection?.twitter_username ? `https://twitter/${asset?.collection?.twitter_username}` : ''}>
                                <SVG src={require("../../assets/images/nft/twitter.svg").default} />
                            </SocialLink>
                            <SocialLink target={'_blank'} rel={'noopener noreferrer'} href={asset?.collection?.medium_username ? `https://medium.com/@${asset?.collection?.medium_username}` : ''}>
                                <SVG src={require("../../assets/images/nft/medium.svg").default} />
                            </SocialLink>
                        </Socials>
                    </CollapseCard>
                    <CollapseCard title={'Chain Info'}>
                        <div className="d-flex flex-column">
                            <ChainRow>
                                <ChainLabel>Contract Address</ChainLabel>
                                <ChainLink href={getEtherscanLink(1, asset?.asset_contract?.address, 'address')}>
                                    {shorten(asset?.asset_contract?.address, 'choice')}
                                </ChainLink>
                            </ChainRow>
                            <ChainRow>
                                <ChainLabel>Token ID</ChainLabel>
                                <ChainValue>{token_id}</ChainValue>
                            </ChainRow>
                            <ChainRow>
                                <ChainLabel>Blockchain</ChainLabel>
                                <ChainValue>Ethereum</ChainValue>
                            </ChainRow>
                        </div>
                    </CollapseCard>
                </Col>
            </Row>
        </Page>
    )
}

export default NFTAsset;
