export const fetchMarkets = () => {
    return async (dispatch) => {
        dispatch(setState(ServerState.Loading, 'markets'));
        try {
            const { markets } = await solo.api.getMarketsV2()
            const transformedMarkets = {};
            const marginMarkets = Object.keys(markets).filter(m => {
                const tokens = m.split('-');
                const perpetualTokens = tokens.filter(t => {
                    return t.split('')[0] === 'P'
                })
                return perpetualTokens.length === 0;
            }).map(m => {
                transformedMarkets[m] = markets[m]
                return markets[m];
            })


            dispatch(setMarkets(transformedMarkets));
            dispatch(setState(ServerState.Done, 'markets'))

            return transformedMarkets;
        } catch(e) {
            dispatch(setState(ServerState.Error, 'markets'));
        }
    }
}

