export const watchOrderbook = (market) => {
    return async (dispatch) => {
        const ws = new DydxSocket();
        try {
            await ws.connect({
                onClose: () => {
                    console.log('closed');
                },
                onError: e => {
                    console.log(`Error: ${e}`)
                    ws.close()
                }
            })
            ws.watchOrderbook({
                market,
                onUpdates: (updates) => {
                    dispatch(updateOrderbooks(updates));
                }
            })
            return ws;
        } catch(e) {
            ws.close();
        }
    }
}

export const disconnectOrderbookUpdates = () => {
    return dispatch => {
        if(ws) {
            ws.close();
            ws = undefined;
        }
    }
}

