export const fetchPositions = (account, market) => {
    return async (dispatch) => {
        dispatch(setState(ServerState.Loading, 'positions'));
        try {
            const res = await axios.get(`https://api.dydx.exchange/v1/positions?owner=${account}&status=OPEN&market=${market}&limit=100`);
            if(res.hasOwnProperty('data')) {
                dispatch(setPositions(res.data.positions));
                dispatch(setState(ServerState.Done, 'positions'))
            }
        } catch(e) {
            dispatch(setState(ServerState.Error, 'positions'));
        }
    }
}

const convertMarketTokens = (market = DEFAULT_MARGIN_MARKET) => {
    let tokens = market.split('-');
    const convertedTokens = tokens.map(t => {
        switch(t.toLowerCase()) {
            case 'eth': {
                return 'WETH';
            }
            case 'btc': {
                return "PBTC";
            }
            case 'link': {
                return "PLINK";
            }
            case 'usd': {
                return 'PUSD';
            }
            default: {
                return t.toUpperCase();
            }
        }
    });
    let convertedMarket = convertedTokens.join('-')
    return convertedMarket;
}

export const fetchMarketStats = (market) => {
    return async (dispatch) => {
        dispatch(setState(ServerState.Loading, 'marketStats'));
        try {
            const res = await axios.get(`https://api.dydx.exchange/v1/stats/markets`);
            if(res.hasOwnProperty('data')) {
                let transformedMarkets = {}
                for(let i in res.data.markets) {
                    let key = convertMarketTokens(i);
                    transformedMarkets[key] = res.data.markets[i];
                }
                dispatch(setMarketStats(transformedMarkets));
                dispatch(setState(ServerState.Done, 'marketStats'))
            }
        } catch(e) {
            dispatch(setState(ServerState.Error, 'marketStats'));
        }
    }
}


export const fetchMarket = (account, market) => {
    return async dispatch => {
        if(ws) {
            dispatch(disconnectOrderbookUpdates())
        }

        let convertedMarket = market || DEFAULT_MARGIN_MARKET;
        let tokens = convertedMarket.split('-');
        const convertedTokens = tokens.map(t => {
            switch(t.toLowerCase()) {
                case 'eth': {
                    return 'WETH';
                }
                case 'btc': {
                    return "PBTC";
                }
                case 'link': {
                    return "PLINK";
                }
                case 'usd': {
                    return 'PUSD';
                }
                default: {
                    return t.toUpperCase();
                }
            }
        });
        convertedMarket = convertedTokens.join('-')

        const markets = await dispatch(fetchMarkets());
        if((markets && !Object.keys(markets).includes(convertedMarket)) || !markets) {
            if(market !== DEFAULT_MARGIN_MARKET) {
                fetchMarket(account, DEFAULT_MARGIN_MARKET);
                return false;
            } else {
                return false;
            }
        }

        // Set new market when did we are sure that was exists on dydx
        dispatch(setSelectedMarket(markets[convertedMarket]));

        // Fetch market data from api
        dispatch(fetchTrades(convertedMarket));
        dispatch(fetchOrderbook(convertedMarket));
        dispatch(fetchFills(convertedMarket));
        dispatch(fetchMarketStats(convertedMarket));

        // Fetch Account data from api
        dispatch(fetchOrders(account, convertedMarket));
        dispatch(fetchPositions(account, convertedMarket))

        // update orderbooks if needed
        if(process.env.REACT_APP_DERIVATIVES_UPDATES) {
            ws = await dispatch(watchOrderbook(convertedMarket));
        }
        return ws;

    }
}

